*{
  margin: 0;
}

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header{
  background: #DD614A;
  margin: 0;
  padding-top: 14px;
  padding-bottom: 14px;
  align-self: stretch;
}

.header .logo{
  margin: auto;
  margin-bottom: -5px;
  display: block;
}

.answer-box{
  display: grid;
  grid-template-columns: auto auto;
  gap: 13px;
  margin-bottom: 15px;
}

.answer-button{
  border: solid;
  gap: 10px;

  width: 145.53px;
  height: 87.84px;

  display: flex;
  align-items: center;
  justify-content: center;

  border: 3px solid rgba(44, 66, 81, 0.15);
  box-sizing: border-box;
  border-radius: 10.4889px;

  font-weight: 600;
  font-size: 18px;
  line-height: 28px;

}

.unselectable{
  -webkit-user-select:none;
  -khtml-user-select:none;
  -moz-user-select:none;
  -ms-user-select:none;
  -o-user-select:none;
  user-select:none;
}

/* .unselectable:hover {
  cursor: default;
} */

.english-from-hangul-game{
  width:93%;
  max-width:400px;
  min-height: 370px;
  
  margin-top: 15px;
  padding: 18px 14px 18px 14px;

  display: flex;
  flex-direction: column;
  align-items: center;

  background: #F4EEE6;
  border: 5.24444px solid #FDF6ED;
  box-sizing: border-box;
  border-radius: 9.17778px;

  color: #2C4251;
  box-shadow: 0px 0px 12px -1px rgba(0, 0, 0, 0.25);

}

.active-game{
  display: flex;
  flex-direction: column;
  align-items: center;
  width:100%;
}



.hangul-game--top-bar{
  display: flex;
  width: 100%;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  align-self: flex-start;
  justify-content: space-between;
  gap: 5%;
  margin-bottom: 0;
  
}

.hangul-game--top-bar p{
  height: 0;
}

.question-letter{
  font-weight: 700;
  font-size: 47.2px;
  line-height: 64px;
  margin-top: 10px;
}

.answer-response{
  font-weight: 400;
  font-size: 13.1111px;
  line-height: 18px;
}

.score{
  font-weight: 600;
  font-size: 17.0444px;
  line-height: 23px;
  align-self: flex-start;
  /* margin-top: auto; */
}

.dot{
  width: 22.29px;
  height: 22.29px;
  background: #97DB4F;
  border-radius: 50%;
  display: block;
}

.lives-bar{
  display: flex;
  gap: 10px;
}

.wrong-answer{
  display: flex;
  
}

.wrong-answer .text{
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  margin-left: 15px;
}

.wrong-answer h3{
  font-weight: 600;
  font-size: 30px;
  line-height: 38px;
}

.ended-game{
  display: flex;
  flex-direction: column;
  /* margin-bottom: 15px; */
  margin-top: -10px;
  /* margin-bottom: auto; */
}

.ended-game>p{
  font-weight: 400;
  font-size: 15px;
  line-height: 20px;
  margin-top: 8px;

  /* height: 0px; */
}

.ended-game>h2{
  font-weight: 700;
  font-size: 20px;
  line-height: 27px;
  margin: 0;
}

.ended-game>ul{
  margin-left: -25px;
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
  flex-direction: column;
  gap:10px;
}

.ended-game>button{
  /* width: 144px;
  height: 30px;  */
  align-self: center;

  display: flex;
  justify-content: space-between;
  align-items: center;

  color: #FFFFFF;
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 27px;

  background: #DD614A;
  border: 2.62222px solid rgba(44, 66, 81, 0.15);
  box-sizing: border-box;
  border-radius: 10.4889px;

}

.hover-button{
  /* box-shadow: 0px 3px 7px 1px rgba(0, 0, 0, 0.25); */
  box-shadow: 0px 2px 7px -1px rgba(0, 0, 0, 0.25);
}

.hover-button:active{
  border: 2.62222px solid rgba(44, 66, 81, 0.15);
  box-sizing: border-box;
  /* box-shadow: inset 0px 4px 10px 3px rgba(0, 0, 0, 0.25); */
  box-shadow: inset 0px 2px 10px 1px rgba(0, 0, 0, 0.25);
  border-radius: 10.4889px;
}

.toggle{
  /* align-self: flex-end; */
  margin-left: auto;
  /* margin-top: 10px; */
  display: flex;
  align-items: center;

  font-weight: 700;
  font-size: 15px;
  line-height: 20px;

  color: #2C4251;
}



.toggle .toggle-case{
  width: 40px;
  /* height: 19px; */
  margin-left: 6px;
  display: inline-flex;
  align-items: center;

  background: #989898;
  border-radius: 50px;
  box-shadow: inset 0px 0px 4px rgba(0, 0, 0, 0.25);
}

.toggle .toggle-switch{
  background: #C3BDAF;
  width: 19px;
  height: 19px;
  border-radius: 50px;
  margin: 2px;

  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);

  /* margin-left: auto; */
  
}

.game-footer{
  width: 100%;
  margin-top: auto;
  display: flex;
}
